.header {
  display: flex;
  flex-direction: row;
  padding: 0em 1em 0em 1em;
  align-items: center;
  &__link {
    margin-right: auto;
    &__title {
      color: var(--primary);
      font-size: 2.3em;
      align-self: center;
      justify-self: center;
    }
  }
  border-bottom: 1px solid var(--primary);
}

.dropdown {
  display: flex;
  justify-content: center;
  padding: 1em;
  gap: 2em;
  overflow: hidden;
  font-family: Arial;
  a {
    float: left;
    font-size: 16px;
    text-align: center;
    text-decoration: none;
  }

  &__section {
    float: left;
    overflow: hidden;

    &:hover .dropdown__section__content {
      display: block;
    }

    &__button {
      color: var(--primary);
      font-size: 1.4em;
      border: none;
      outline: none;
      padding: 0px 5px;
      background-color: inherit;
      font-family: inherit;
      margin: 0;
      margin-bottom: 5px;
      cursor: pointer;
      transition: all 0.3s ease-in-out;
      &:hover {
        color: var(--accent);
      }
    }

    &__content {
      display: none;
      position: absolute;
      right: 1em;
      background-color: #f9f9f9;
      min-width: 60px;
      z-index: 1;
      p {
        transition: all 0.3s ease-in-out;
        cursor: pointer;
        background-color: var(--background);
        float: none;
        color: var(--primary);
        padding: 12px 16px;
        text-decoration: none;
        display: block;
        text-align: left;

        &:hover {
          color: var(--accent);
          background-color: var(--background);
        }
      }
    }
  }
}
